.globalCasesCard {
    box-sizing: border-box;
    width: 100%;
    background: rgba(255,255,255,.2);
    border: solid 2px rgba(255,255,255,.1);
    padding: 5px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    max-height: 90px;
    text-align: center;

    font-weight: 100;
}

.globalCasesCard h2{
    margin-bottom: 10px;
    margin-top: 0;
    font-size: 17px;
    font-weight: 200;
}

.card-cases {
    color: red;
    font-size: 25px;
    font-weight: 700;
}